.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.cardImages {
  width: 200px;
  height: 375px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.text {
  text-align: justify;
  font-size: 20px;
}

.textBox {
  position: relative;
  opacity: 0;
  left: 500px;
  bottom: 690px;
  height: 50px;
  width: 970px;
}

.textAboutBox {
  background-color: #818181;
  opacity: 0;
  height: 15%;
  width: 120%;
}

.App-link {
  color: #61dafb;
}

.homeTextContainer {
  justify-content: center;
}

.subHomeContainer {
  background-color: white;
  justify-content: center;
  margin-left: 15%;
  opacity: 0.8;
  height: 500px;
  width: 1500px;
}

.content-wrapper {
  height: 800px;
  width: 100%;
  background-image: url(components/backgroundGrey.jpg);
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.manImage {
  opacity: 0;
  width: 10%;
}

.g-card-info {
  margin-top: 40px;
  min-height: 100px;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.footer {
  flex: 1;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
